// Header.jsx
import React, { useState } from 'react';
import '../../asset/css/style.css';
import icon from '../../asset/icon/icon (2).png';
import { Icon } from '@iconify/react';
import { Link } from 'react-router-dom';

function Header() {
    const [showMenu, setShowMenu] = useState(false);
    // Inside your component, toggle the 'active' class on the menu
    const handleShowNavbar = () => {
        setShowMenu(!showMenu);
        const navBarTwo = document.querySelector('.nav-bar-two');
        navBarTwo.classList.toggle('active', !showMenu);
    };

    const closeMenu = () => {
        setShowMenu(false);
    };

    return (
        <div>
            <nav className={`nav-background`}>
                <div className="lol-container">
                    <div className="nav-bar">
                        <div className="nav-bar-one">
                            <a href="/dashboard">
                                <img style={{ width: '50px', height: '50px' }} src={icon} alt="" />
                            </a>
                            <div className={`nav-bar-two ${showMenu && 'active'}`}>
                                <Link to={'/dashboard'} onClick={closeMenu} className='nav-bar-tow-hover hover-underline-animation'>Home</Link>
                                <Link to={'/about-page'} onClick={closeMenu} className='nav-bar-tow-hover hover-underline-animation'>About</Link>
                                <Link to={'/skill-page'} onClick={closeMenu} className='nav-bar-tow-hover hover-underline-animation'>Skill</Link>
                                <Link to={'/project-page'} onClick={closeMenu} className='nav-bar-tow-hover hover-underline-animation'>Project</Link>
                                <Link to={'/contact-page'} onClick={closeMenu} className='nav-bar-tow-hover hover-underline-animation'>Contact</Link>
                            </div>
                        </div>
                        <button className="menu-button" onClick={handleShowNavbar}>
                            <Icon icon="typcn:th-menu-outline" width="40" height="40" />
                        </button>
                    </div>
                </div>
            </nav>
        </div>
    );
}

export default Header;
